body {
    font-family: "Roboto", sans-serif;
    background: #EFF1F3;
    min-height: 100vh;
    position: relative;
  }
  
  .section-50 {
    padding: 50px 0;
  }
  
  .m-b-50 {
    margin-bottom: 50px;
  }
  
  .dark-link {
    color: #333;
  }
  
  .heading-line {
    position: relative;
    padding-bottom: 5px;
  }
  
  .heading-line:after {
    content: "";
    height: 4px;
    width: 75px;
    background-color: #299078;
    position: absolute;
    bottom: 0;
    left: 0;
  }
  
  .notification-ui_dd-content {
    margin-bottom: 30px;
  }
  
  .notification-list {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    margin-bottom: 7px;
    background: #fff;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.06);
  }
  
  .notification-list--unread {
    border-left: 2px solid #299078;
  }
  
  .notification-list .notification-list_content {
    display: flex;
  }
  
  .notification-list .notification-list_content .notification-list_img img {
    height: 48px;
    width: 8px;
    border-radius: 50px;
    margin-right: 20px;
  }
  
  .notification-list .notification-list_content .notification-list_detail p {
    margin-bottom: 5px;
    line-height: 1.2;
  }
  
  .notification-list .notification-list_feature-img img {
    height: 48px;
    width: 48px;
    border-radius: 5px;
    margin-left: 20px;
  }
  
  
